import { Component, OnInit } from '@angular/core';

// 3rd party
import { TranslateService } from '@ngx-translate/core';
import { Ci18nImage } from 'src/app/shared/models/ci18n-image';

@Component({
  selector: 'app-credit',
  templateUrl: './credit.component.html',
  styleUrls: ['./credit.component.scss']
})
export class CreditComponent implements OnInit {

  public bError = false;
  public supporters: Ci18nImage[] = [{
      ALT: '',
      SRC: ''
  }];

  constructor(public translate: TranslateService) { }

  ngOnInit() {
      this.translate.get('RESEARCH.CREDITS.SUPPORTER.SUPPORTERS').subscribe((res: Ci18nImage[] | string) => {
          if (typeof(res) === 'string') {
              this.bError = true;
              this.supporters = [{
                      ALT: '',
                      SRC: ''
              }];
          } else {
              this.supporters = res as Ci18nImage[];
          }
      });
  }
}
