import { Component, OnInit } from '@angular/core';


import { TranslateService } from '@ngx-translate/core';
import { Ci18nPublication } from 'src/app/shared/models/ci18n-publication';


@Component({
  selector: 'app-publications',
  templateUrl: './publications.component.html',
  styleUrls: ['./publications.component.scss']
})
export class PublicationsComponent implements OnInit {

  public bError = false;

  // Team Carousel
  public publications: Ci18nPublication[] = [{
      TITLE: 'Loading',
      SUBTITLE: 'Loading...',
      LINK: 'Loading...',
      IMG: {
          ALT: '',
          SRC: ''
      }
  }];

  constructor(public translate: TranslateService) { }

  ngOnInit() {
    this.translate.get('RESEARCH.PUBLICATIONS.PUBLICATION').subscribe((res: Ci18nPublication[] | string) => {
      if (typeof(res) === 'string') {
          this.bError = true;
          this.publications = [{
              TITLE: 'An error occurred',
              SUBTITLE: 'Cannot get translation',
              LINK: 'Cannot get translation',
              IMG: {
                  ALT: '',
                  SRC: ''
              }
          }];
      } else {
          this.publications = res as Ci18nPublication[];
      }
  });

  }

}
